.counter__box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;


}
.value__container{
    padding: 15px 35px;
    margin-bottom: 50px;
    border: 1px solid var(--color-primary-variant);
    border-radius: 30px;
    font-size: 30px;
}

.value__container:hover{
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

